// @flow weak

import React, {
  PureComponent
}                     from 'react';
import PropTypes      from 'prop-types';
import {Jumbotron}    from '../../components';
import AnimatedView   from '../../components/animatedView/AnimatedView';
import ReactSelect from 'react-select';
import { getCategories } from '../../services'

class TryAutocomplete extends PureComponent {
  static propTypes= {
    // react-router 4:
    match:    PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    history:  PropTypes.object.isRequired
  };

  constructor(props) {
    super(props)

    this.state = {
      currentSelection: ''
    }

    this._onChange = this._onChange.bind(this)
  }

  componentDidMount () {
    getCategories()
  }

  handleInputChange (elements) {
    const { fields } = this.props
    const { livingAreas } = fields

    this.setState({ livingAreaInput: elements })

    const livingAreasValues = Object.values(elements).map(({ value }) => value).join(',')
    livingAreas.onChange(livingAreasValues)
  }

  _onChange (elements) {
    this.setState({ currentSelection: elements })
  }

  render() {
    const { currentSelection } = this.state
    const options = [
      {label: 'Maia', value: 'Diego'}
    ]
    return(
      <AnimatedView>
        <Jumbotron>
          <h3>
            Autocomplete Input
          </h3>
          <div>
            <ReactSelect
              name='form-input-select'
              multi
              searchable
              ignoreCase
              placeholder='Start typing to filter...'
              value={currentSelection}
              options={options}
              onChange={this._onChange}
            />
          </div>
        </Jumbotron>
      </AnimatedView>
    );
  }
}

export default TryAutocomplete;



